<template>
  <div class="diglog">
    <el-dialog
      :title="dialog.title"
      :visible.sync="dialog.show"
      :close-on-click-modal="false"
      :modal-append-to-body="false"
    >
      <div class="form">
        <el-form
          ref="form"
          :model="formdata"
          :rules="form_rules"
          label-width="120px"
          style="margin:10px;width:auto"
        >
          <el-form-item prop="type" label="收支类型">
            <el-select v-model="formdata.type">
              <el-option
                v-for="(formtype,index) in format_type_list"
                :key="index"
                :lable="formtype"
                :value="formtype"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="describe" label="收支描述">
            <el-input type="describe" v-model="formdata.describe"></el-input>
          </el-form-item>
          <el-form-item prop="income" label="收入:">
            <el-input type="income" v-model="formdata.income"></el-input>
          </el-form-item>

          <el-form-item prop="expend" label="支出:">
            <el-input type="expend" v-model="formdata.expend"></el-input>
          </el-form-item>

          <el-form-item prop="cash" label="账户现金:">
            <el-input type="cash" v-model="formdata.cash"></el-input>
          </el-form-item>

          <el-form-item label="备注:">
            <el-input type="textarea" v-model="formdata.remark"></el-input>
          </el-form-item>

          <el-form-item class="text_right">
            <el-button @click="dialog.show = false">取消</el-button>
            <el-button type="primary" @click="onSubmit('form')">提交</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "dialog",
  data() {
    return {
      format_type_list: ["提现", "充值", "优惠", "转账"],
      form_rules: {
        type: [
          { required: true, message: "请选择收支类型！", trigger: "blur" }
        ],
        describe: [
          { required: true, message: "收支描述不能为空！", trigger: "blur" }
        ],
        income: [
          { required: true, message: "收入不能为空！", trigger: "blur" }
        ],
        expend: [
          { required: true, message: "支出不能为空！", trigger: "blur" }
        ],
        cash: [{ required: true, message: "账户不能为空！", trigger: "blur" }]
      }
    };
  },
  props: {
    dialog: Object,
    formdata: Object
  },
  methods: {
    onSubmit(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          const url =
            this.dialog.option == "add" ? "add" : `edit/${this.formdata.id}`;
          this.$axios.post(`/api/profiles/${url}`, this.formdata).then(res => {
            this.$message({ message: "添加成功", type: "success" });
          });
          this.dialog.show = false;
          this.$emit("update");
        }
      });
    }
  }
};
</script>
<style scoped>
</style>